<template>
  <div style="margin: 10px">
    <h1 style="color: white;">유령결사 행운의 제비뽑기!</h1>
    <div style="display:flex; justify-content: center" >
      <div style="margin-right: 10px"><span>총 인원: </span><input type="number" v-model="members" style="width: 25px"></div>
      <div><span>당첨 인원: </span><input type="number" v-model="winners" style="width: 25px"></div>
    </div>
    <button @click="onDraw" style="margin-top: 20px">추첨하기</button>
    <div v-if="prizeWinner.length > 0">
      <h2 style="color: white;">당첨자 번호</h2>
      <ul>
        <li v-for="winner in prizeWinner" :key="winner" style="color: white;">{{ winner }}</li>
      </ul>
    </div>
  </div>
</template>

<script>
export default {
  name: 'GhostDraw',
  data () {
    return {
      members: 0,
      winners: 0,
      prizeWinner: []
    }
  },
  methods: {
    onDraw() {
      this.prizeWinner = [];
      if (this.members !== 0 && this.winners !==0 && this.winners < this.members) {
        const shuffledMembers = [...Array(this.members).keys()].map(x => x + 1); // [1, 2, 3, ..., members]
        for (let i = 0; i < this.winners; i++) {
          const randomIndex = Math.floor(Math.random() * shuffledMembers.length);
          const winnerNumber = shuffledMembers.splice(randomIndex, 1)[0];
          this.prizeWinner.push(winnerNumber);
        } 
      } else {
        alert('인원을 다시 확인해주세요!')
      }
    }
  }
}
</script>

